.navStyle {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 30px;
    font-family: "Arial Black", Gadget, sans-serif;
}
#itemName {
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    padding: 3px;
    display: inline-block;
    color: white;
    margin-left: 50px;
    font-size: 30px;
}

#itemName:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}
#background {
  background: -webkit-linear-gradient(315deg, rgb(0, 0, 0, 1) 30%, rgba(255, 255, 255, 0) 100%);
  background: linear-gradient(135deg, rgb(0, 0, 0, 1) 30%, rgba(255, 255, 255, 0) 100%);;
}

h1 {
  text-align: center;
  color: white;
  font-size: 35px;
}

.footerStyle {
    display: flex;
    justify-content: flex-end;
    color: white;
    font-size: 30px;
}
